<template>
    <div>1、state ——》储存初始化数据 2、getters ——》对State 里面的数据二次处理（对数据进行过滤类似filter的作用）比如State返回的为一个对象，我们想取对象中一个键的值用这个方法
        3、mutations ——》对数据进行计算的方法全部写在里面（类似computed） 在页面中触发时使用 this.$store.commit('mutationName')触发Mutations方法改变state的值
        4、actions ——》 处理Mutations中已经写好的方法 其直接触发方式是 this.$store.dispatch(actionName)        5、modules ——》模块化Vuex
    </div>
</template>

<script>
export default {
  name: 'vueStore'
}
</script>

<style scoped>

</style>



